<template>
  <div id="app">
    <div class="row main">
      <div class="col-md-3 col-sm-1 col-1"></div>
      <div class="col-md-6 col-sm-10 col-10">
        <h1 class="title">Moore</h1>
        <h1 class="title">Hero</h1>
        <h5 class="option">
          <a href="https://polygonscan.com/address/0x1d9A1FEe33fbE7E587d4882C7d69E5c9aD188830#writeContract"
             target="_blank">
            contract</a>
        </h5>
        <p class="text">
          MooreHero is randomized moore character generated and stored on chain.
          <br/>
          Stats, images, and other functionality are intentionally omitted for others to interpret.
          <br/>
          Feel free to use MooreHero in any way you want.</p>
      </div>
    </div>
    </div>
</template>

<script>
  export default {
    name: 'App',
    data() {
      return {
        address: "",
      }
    },
    methods: {
    }
  }
</script>

<style>
#app {
  padding: 0;
  margin: 0;
  color: #fff;
  background-color: #1a1a1a;
  font-family: EB Garamond,serif,sans-serif;
  overflow-x: hidden;
}
.main{
  margin-top: 50px;
}
.title{
  text-align: center;
  font-size: 5rem;
}
.option{
  text-align: center;
}
.option a{
  transition: .3s;
}
.option a:hover{
  cursor: pointer;
  padding-bottom: 3px;
  border-bottom: 1px solid white;
}
  .text{
    color:#8e8e8e;
    text-align: center;
    margin: 0 auto;
  }
</style>



